#NHome .content .container{
    /* background-color: #f8f8f8; */
    /* height: 150vh; */
    padding-top: 150px;
    padding-bottom: 100px;
}

#NHome .content .wrap-title{
    /* padding: 0 7%; */
}

#NHome .content .title{
    width: 50%;
    font-size: 30px;
    font-weight: 400;
    line-height: 1.3;
    font-family: "font-title";
}

#NHome .content .body{
    width: 25%;
}

#NHome .content .line-about-me{
    width: 200vw;
    height: 1px;
    top: 50%;
    background-color: #1D1C20;
}

#NHome .content .wrap-circle-about-me{
    height: 250px;
}

#NHome .content .circle-about-me{
    padding: 70px 45px;
    font-size: 15px;
    background-color: #1D1C20;
    top: 0;
    margin-top: 40px !important;
    right: 23%;
    font-weight: 400;
    transition: background-color 1s;
}

/* #NHome .content .circle-about-me:hover{
    background-color: #D5FF3F;
} */

#NHome .content .wrap-selected-porto .title{
    padding-left: 70px;
    font-size: 30px;
    font-family: "font-title";
}

#NHome .content .box-porto{
    width: 48%;
}

#NHome .content .porto{
    /* height: 450px; */
    padding: 0;
    margin-bottom: 170px;
}

#NHome .content .porto-3,
#NHome .content .porto-4{
    margin-bottom: 100px;
}

#NHome .content .porto h2{
    font-family: 'font-title';
    border-bottom: solid 1px #7d7d7d;
    padding: 20px 0;
}

#NHome .content .porto .wrap-info{
    padding-top: 10px;
}

#NHome .content .photo-porto{
    /* background-position: center;
    background-size: cover; */
    /* height: 400px; */
    padding: 110px 45px;
    background-color: #d3d3d3;
}

#NHome .content .photo-porto div{
    background-position: center;
    background-size: cover;
    /* height: 400px; */
    aspect-ratio: 16/11;
}

#NHome .wrap-more-porto{
    height: 70px;
}

#NHome .more-porto{
    /* padding: 70px 40px; */
    /* background-color: #1D1C20; */
    border: solid 1px rgb(91, 91, 91);
    padding: 20px 30px;
    transition: background-color 0.5s;
}

#NHome .more-porto:hover{
    background-color: #1D1C20;
    color: white !important;
}

@media only screen and (min-width: 768px) and (max-width: 1024px){
    #NHome .content .container{
        /* background-color: #f8f8f8; */
        /* height: 150vh; */
        /* padding-top: 100px; */
    }
    
    #NHome .content .wrap-title{
        /* padding: 0 7%; */
        flex-wrap: wrap;
    }
    
    #NHome .content .title{
        width: 100%;
        font-size: 35px;
        /* font-weight: 400; */
        margin-bottom: 10px !important;
    }
    
    #NHome .content .body{
        width: 100%;
        font-size: 20px;
    }
    
    #NHome .content .line-about-me{
        /* width: 200vw;
        height: 1px;
        top: 50%; */
        /* background-color: #1d1c206d; */
        display: none;
    }
    
    #NHome .content .wrap-circle-about-me{
        height: 250px;
        border: solid 1px black;
    }
    
    #NHome .content .circle-about-me{
        padding: 70px 45px;
        font-size: 15px;
        background-color: #1D1C20;
        top: 0;
        right: auto;
        left: 0;
        font-weight: 400;
    }  
    
    #NHome .content .wrap-selected-porto .title{
        padding-left: 50px;
        font-size: 35px;
        font-family: "font-title";
    }

    #NHome .line-more-porto{
        display: none;
    }
    
    #NHome .content .porto{
        /* height: 450px; */
        padding: 0;
        margin-bottom: 50px;
    }
}

@media only screen and (max-width: 767px){
    #NHome .content .container{
        /* background-color: #f8f8f8; */
        /* height: 150vh; */
        /* padding-top: 40px; */
    }
    
    #NHome .content .wrap-title{
        /* padding: 0 7%; */
        flex-wrap: wrap;
    }
    
    #NHome .content .title{
        width: 100%;
        font-size: 26px;
        /* font-weight: 400; */
        margin-bottom: 40px !important;
    }
    
    #NHome .content .body{
        width: 100%;
        font-size: 16px;
    }
    
    #NHome .content .line-about-me{
        /* width: 200vw;
        height: 1px;
        top: 50%; */
        /* background-color: #1d1c206d; */
        display: none;
    }
    
    #NHome .content .wrap-circle-about-me{
        height: 145px;
    }
    
    #NHome .content .circle-about-me{
        padding: 50px 25px;
        font-size: 15px;
        /* background-color: #1D1C20; */
        top: 0;
        right: auto;
        left: 0;
        font-weight: 400;
        margin-top: 0 !important;
    }

    #NHome .content .box-porto{
        width: 100%;
    }

    #NHome .content .wrap-selected-porto .title{
        padding-left: 0;
        text-align: end;
        font-size: 20px;
        font-family: "font-title";
        margin-bottom: 10px !important;
        margin-top: 50px;
    }

    #NHome .content .photo-porto{
        /* background-position: center;
        background-size: cover; */
        /* height: 400px; */
        padding: 80px 20px;
        background-color: #d3d3d3;
    }

    #NHome .porto h2{
        font-size: 24px;
    }

    #NHome .porto-1{
        margin-bottom: 100px !important;
    }

    #NHome .porto-2{
        margin-bottom: 40px !important;
    }

    #NHome .porto-3,
    #NHome .porto-4{
        display: none;
    }

    #NHome .line-more-porto{
        display: none;
    }

    #NHome .wrap-more-porto{
        height: 130px;
        justify-content: start !important;
    }

    #NHome .more-porto{
        padding: 50px 20px;
        background-color: #1D1C20;
        color: white;
        font-size: 15px;
    }
    
}

